.auth-modal {
  border: 2px solid var(--title-text-color-theme-light);
  width: 90vw;
  max-width: 375px;
  transition: var(--transition);
  background-color: #f6f6f6;
  border-radius: 18px;
  padding: 40px 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 768px) {
  .auth-modal {
    max-width: 530px;
    padding: 80px 40px 40px;
  }
}

@media screen and (min-width: 1440px) {
  .auth-modal {
    max-width: 580px;
  }
}

.input-wrapper {
  margin-bottom: 24px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .input-wrapper {
    margin-bottom: 28px;
  }

  .input-wrapper:nth-child(3) {
    margin-bottom: 40px;
  }
}

.modal-icon {
  stroke: var(--logo-text-color);
  transition: stroke var(--animation);
  position: absolute;
  top: 18px;
  right: 24px;
}

@media screen and (min-width: 768px) {
  .modal-icon {
    width: 24px;
    height: 24px;
    top: 15px;
  }
}

@media screen and (min-width: 1440px) {
  .modal-icon {
    width: 28px;
    height: 28px;
    top: 18px;
    right: 32px;
  }
}

.modal-input {
  width: 100%;
  border: 2px solid var(--title-text-color-theme-light);
  transition: border var(--animation);
  background-color: #f6f6f6;
  border-radius: 40px;
  padding: 16px 24px;
  font-weight: 700;
  line-height: 1.12;
}

.modal-input:focus {
  border-color: var(--accent-color);
  outline: rgba(0, 0, 0, 0);
}

.modal-input:focus ~ .modal-icon {
  stroke: var(--accent-color);
}

.modal-input::-ms-input-placeholder {
  text-transform: uppercase;
  color: var(--title-text-color-theme-light);
}

.modal-input::placeholder {
  text-transform: uppercase;
  color: var(--title-text-color-theme-light);
}

@media screen and (min-width: 1440px) {
  .modal-input {
    padding: 20px 40px;
    font-size: 18px;
  }
}

.modal-submit-btn {
  cursor: pointer;
  width: 100%;
  text-transform: uppercase;
  background: var(--title-text-color-theme-light);
  color: #f3f3f3;
  transition: background-color var(--animation), outline-offset var(--animation), color var(--animation);
  border-style: none;
  border-radius: 40px;
  padding: 20px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.33;
}

.modal-submit-btn:hover, .modal-submit-btn:focus {
  background-color: var(--accent-color);
}

.modal-sign-btn {
  cursor: pointer;
  letter-spacing: -.01em;
  text-transform: uppercase;
  color: var(--title-text-color-theme-lgiht);
  background-color: rgba(0, 0, 0, 0);
  border-style: none;
  padding: 0;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.33;
}

.modal-sign-btn.is-active {
  color: var(--accent-color);
  text-decoration: underline;
}

.modal-sign-btn-wrapper {
  justify-content: center;
  column-gap: 20px;
  margin-top: 14px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .modal-sign-btn-wrapper {
    margin-top: 20px;
  }
}

.modal-close-icon {
  stroke: var(--title-text-color-theme-light);
  transition: stroke var(--animation);
}

@media screen and (min-width: 768px) {
  .modal-close-icon {
    width: 14px;
    height: 14px;
  }
}

.modal-close-btn {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 14px;
  right: 14px;
}

.modal-close-btn:hover .modal-close-icon, .modal-close-btn:focus .modal-close-icon {
  stroke: var(--accent-color);
}

@media screen and (min-width: 768px) {
  .modal-close-btn {
    top: 24px;
    right: 24px;
  }
}

.basic-auth.basicLightbox {
  z-index: 1004;
  background: radial-gradient(circle at -850 -850, #ccece7 -150, var(--accent-color), var(--accent-color) 130%), no-repeat;
}

.auth-modal-wrapper {
  width: 100vw;
  height: 100vh;
  background-color: var(--accent-color);
  z-index: 1000;
  backdrop-filter: blur(3px);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

